.frame {
    min-height: 100vh;
    height: fit-content;
    position: relative;
}

.frame-content {
    height: fit-content;
    /* height: auto; */
    position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
    height: 400px;
    /* background-color: whitesmoke; */
}

.header {
    text-align: center;
    font-size: 23px;
    font-weight: 700;
    margin-top: 50px;
    margin-bottom: 50px;
}

.container-info {
    max-width: 600px;

    position: relative;
    left:50%;
    transform: translateX(-50%);
}

.ci-header {
    margin-top: 30px;
    font-size: 20px;
}

@media screen and (max-width: 800px) {
    .ci-header {
        margin-top: 10px;
    }
    .container-info {
        margin-left: 10px;
        margin-right: 10px;
    }
  }