.vp-back-container {
    width: 100%;
    background-color: whitesmoke;
    padding: 5px;
    padding-left: 15px;
    height: 50px;
    display: flex;
    align-items: center;
    margin-top: 10px;

    border-radius: 10px;
    cursor: pointer;
}

.vp-back-container:hover > .vp-back-arrow {
    transform:  translateX(-5px);
}
.vp-back-container:hover > .vp-back-text {
    transform:  translateX(-5px);
}

.vp-back-arrow {
    height: 17px;
    transition: 0.2s ease;
}

.vp-back-text {
    font-size: 18px;
    margin-left: 5px;
    transition: 0.2s ease;
}

.vp-product-container {
    margin-top: 20px;
    display: grid;

    grid-template-columns: 150px 1fr;

    grid-template-areas:
    "img info";
}

.vp-info-container {
    grid-area: info;
    padding-left: 20px;
}

.vp-img {
    width: 150px;
}

.vp-name {
    font-size: 22px;
}

.vp-price {
    font-weight: 800;
    position: relative;
    bottom: 7px;
    font-size: 19px;
}

.vp-description {
    position: relative;
    bottom: 10px;
}

.vp-button {
    cursor: pointer;
    border-radius: 15px;
    padding: 7px;
    padding-left: 10px;
    padding-right: 10px;
    border-color: black;
    background-color: black;
    color: white;
    transition: 0.2s ease;
}

.vp-button:hover {
    background-color: white;
    color: black;
}

.review-rating {
    margin-left: 10px;
}

.review-container {
    border-radius: 10px;
    background-color: whitesmoke;
    padding: 10px;
    margin-bottom: 5px;
}

.bold {
    font-weight: 700;
}

.lr-margin {
    position: relative;
    left: 10px;
}

.publish-button {
    float: right;
    cursor: pointer;
    border-radius: 15px;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    border-color: black;
    background-color: whitesmoke;
    color: black;
    transition: 0.2s ease;
    margin-top: 10px;
}

.publish-button:hover {
    background-color: black;
    color: whitesmoke;
}

.range { 
    margin: auto;
    -webkit-appearance: none;
    position: relative;
    overflow: hidden;
    height: 10px;
    width: 150px;
    border-radius: 10px;
}

.pointer {
    cursor: pointer;
}

::-webkit-slider-runnable-track {
    background: #C4C4C4;
}

::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 10px; /* 1 */
    height: 10px;
    background: red;
    box-shadow: -100vw 0 0 100vw red;
}

::-moz-range-track {
    height: 10px;
    background: #ddd;
}

::-moz-range-thumb {
    background: #fff;
    height: 10px;
    width: 10px;
    border: 3px solid #999;
    border-radius: 0 !important;
    box-shadow: -100vw 0 0 100vw red;
    box-sizing: border-box;
}

::-ms-fill-lower { 
    background: red;
}

.lime::-webkit-slider-thumb {
    background: lime;
    box-shadow: -100vw 0 0 100vw lime;
}

.lime::-moz-range-thumb {
    box-shadow: -100vw 0 0 100vw lime;
}

.lime::-ms-fill-lower {
    background-color: lime;
}

.green::-webkit-slider-thumb {
    background: rgb(179, 255, 0);
    box-shadow: -100vw 0 0 100vw rgb(179, 255, 0);
}

.green::-moz-range-thumb {
    box-shadow: -100vw 0 0 100vw rgb(179, 255, 0);
}

.green::-ms-fill-lower {
    background-color: rgb(179, 255, 0);
}

.orange::-webkit-slider-thumb {
    background: orange;
    box-shadow: -100vw 0 0 100vw orange;
}

.orange::-moz-range-thumb {
    box-shadow: -100vw 0 0 100vw orange;
}

.orange::-ms-fill-lower {
    background-color: orange;
}

::-ms-thumb { 
    background: #fff;
    border: 2px solid #999;
    height: 10px;
    width: 10px;
    box-sizing: border-box;
}

::-ms-ticks-after { 
    display: none; 
}

::-ms-ticks-before { 
    display: none; 
}

::-ms-track { 
    background: #fff;
    color: transparent;
    height: 10px;
    border: none;
}

::-ms-tooltip { 
    display: none;
}