@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;

  font-family: 'Poppins', 'Monserrat', sans-serif;

  overflow-x: hidden;
  /* border: 1px solid black; */
}

::-webkit-scrollbar {
  display: none;
}



.frame {
    min-height: 100vh;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
}

.frame-content {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    /* height: auto; */
    position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
    height: 400px;
    /* background-color: whitesmoke; */
}

.header {
    text-align: center;
    font-size: 23px;
    font-weight: 700;
    margin-top: 50px;
    margin-bottom: 50px;
}

.container-info {
    max-width: 600px;

    position: relative;
    left:50%;
    transform: translateX(-50%);
}

.ci-header {
    margin-top: 30px;
    font-size: 20px;
}

@media screen and (max-width: 800px) {
    .ci-header {
        margin-top: 10px;
    }
    .container-info {
        margin-left: 10px;
        margin-right: 10px;
    }
  }
.card-panel {
    background: rgb(0,158,226);
    /* background: linear-gradient(135deg, rgba(0,158,226,1) 0%, rgba(2,215,228,1) 35%, rgba(0,232,190,1) 100%); */
    background: linear-gradient(-45deg, #20cceb, #20cceb, #00ffc8, #23a6d5, #23d5ab);
    animation: gradient 15s ease infinite;
    background-size: 400% 400%;

    position: relative;

    background-color: turquoise;
    color: white;
    width: 330px;
    height: 200px;

    margin: auto;

    border-radius: 20px;

    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.card-num {
    font-size: 20px;
    line-height: 20px;
    height: 25px;
    position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
    letter-spacing: 5px;
    text-align: center;
}

.card-info {
    position: absolute;
    bottom: 10px;
    left: 10px;

    font-size: 15px;
    font-weight: 700;
}
.button {
    margin-top:10px;
    margin-left: 50%;
    transform: translateX(-50%);

    color: white;
    background-color: black;

    display: inline-block;

    height: 40px;
    padding: 10px;
    padding-left: 25px;
    padding-right: 25px;

    border-radius: 15px;
    border-style: none;

    cursor: pointer;

    transition: 0.2s ease;
}

.button:hover {
    background-color: white;
    color:black;
    outline: 2px black solid;
    outline-offset: -2px;
}

.disabled {
    cursor: not-allowed;
    background-color: rgb(199, 199, 199);
    color: gray;
}

.disabled:hover {
    cursor: not-allowed;
    background-color: rgb(199, 199, 199);
    color: gray;
    outline: none;
}

.center {
    text-align: center;
}

.main-container {
    padding-top: 100px;
    padding-bottom: 10px;
    margin: auto;
    min-height: 100vh;
    max-width: 700px;
    overflow: visible;
}

@media screen and (max-width: 700px) {
    .main-container {
        padding-left: 15px;
        padding-right: 15px;
        margin: auto;
        max-width: 700px;
    }
}

.partition-name {
    font-size: 20px;
    font-weight: 700;
    vertical-align: middle;
}

.partition-container {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    height:50px;
    align-items: center
}

.profile-pic {
    width: 50px;
    height: 50px;
    border-radius: 10px;
    margin-right: 10px;
}

.initial-letter {
    text-align: center;
    vertical-align: middle;
    line-height: 75px;
