}

.wide-card {
    width: 100%;
    background: linear-gradient(-45deg, #20cceb, #20cceb, #00ffc8, #2dbdf1, #23d5ab);
    animation: gradient 45s ease infinite;

    background-size: 400% 400%;
    position: relative;
    
    background-color: turquoise;
    color: white;
    height: 200px;
    
    margin: auto;
    
    border-radius: 20px;
    
    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.wc-amount {
    font-size: 20px;
    font-weight: 200;
    position: relative;
    top:30px;
    left:30px;
}

.wc-system {
    position: absolute;
    left:30px;
