}

.transaction-card {
    margin-bottom: 10px;
    transition: 0.2s ease;

    display: grid;

    grid-template-columns: 75px 1fr 100px;
    grid-template-rows: 40px 1fr;

    grid-template-areas: 
    "img name amount"
    "img desc amount";
}

.transaction-card:hover {
    border-radius: 5px;
    background-color: whitesmoke;
}

.ts-img {
    grid-area: img;
    
    background: rgb(222,222,222);
    
    width:75px;
    height:75px;

    border-radius: 20px;
}

.ts-name {
    grid-area: name;

    margin-top: 10px;
    margin-left: 10px;

    font-size: 18px;
    font-weight: 600;
}

.ts-desc {
    margin-left: 10px;
    position: relative;
    top:-10px;

    font-size: 17px;
    font-weight: 400;

    grid-area: desc;
}

.ts-amount {
    grid-area: amount;

    text-align: end;
    
    display: flex;
    justify-content: right;
    align-items: center;
}

