.promo-card {
    width: 100%;
    background: url("https://www.pngkey.com/png/full/7-77287_snow-png-transparent-image-ivory.png") repeat, url("https://www.pngkey.com/png/full/7-77287_snow-png-transparent-image-ivory.png") repeat, linear-gradient(135deg,#20cceb, #23d5ab);
    background-size: 500px 500px, 500px 500px, 100%;

    position: relative;
    height: 200px;
    margin: auto;
    border-radius: 20px;
    
    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);

    animation: snow 10s linear infinite;

    color: white;
}

@keyframes snow {
  0% {background-position: 0px 0px, 0px 0px, 0px 0px;}
  50% {background-position: 500px 500px, 250px 250px, 0px 0px;}
  100% {background-position: 1000px 1000px, 500px 500px, 0px 0px;}
}

.promo-name {
  margin-top: 70px;
  margin-left: 30px;

  font-size:30px;
  font-weight: 700;
}

.promo-description {
  margin-left: 30px;
  font-weight: 300;

  position: relative;
  bottom: 10px;
}

.product-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 7px;
  grid-row-gap: 7px;
  padding-bottom: 10px;
}

.product-card {
  padding: 10px;
  padding-top: 15px;
  padding-bottom: 0px;
  background-color: whitesmoke;
  border-radius: 7px;

  transition: 0.2s ease;
}

.product-card:hover > .product-img{
  transform: translateY(-10px);
}

.product-card:hover > .product-name{
  transform: translateY(-10px);
}

.product-card:hover > .product-price{
  transform: translateY(-10px);
}

.product-card:hover > .product-detail{
  transform: translateY(-10px);
}

.product-card:hover > .product-button{
  opacity: 1.0;
  transform: translateY(-10px);
}

.product-button {
  opacity: 0.0;
  cursor: pointer;
  border-radius: 10px;
  width: 100%;
  transition: 0.2s ease;
  border: 1px solid black;
}

.product-button:hover{
  background-color: rgb(221, 221, 221);
}

.edit-button-container {
  padding-top: 10px;
  padding-bottom: 10px;
}

.edit-button {
  cursor: pointer;
  border-radius: 10px;
  transition: 0.2s ease;
  border: 1px solid black;
  width: 100%;
}

.edit-button:hover {
  background-color: rgb(221, 221, 221);
}

.create-button {
  margin-top: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  border-radius: 10px;
  width: 100%;
  transition: 0.2s ease;
}

.product-card img{
