  margin-right: auto;
  transition: 0.2s ease;
}

.product-name {
  font-weight:400;
  font-size: 13pt;
  transition: 0.2s ease;
}

.product-price {
  font-weight:800;
  font-size: 13pt;
  transition: 0.2s ease;
}

.product-detail {
  font-size: 8pt;
