}

.product-status-button {
  background-color: transparent;
  outline: transparent;
  border: 0px;
  padding: 5px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s ease;
  border-radius: 4px;
}

.product-status-button:hover {
