}

.product-button-save {
  grid-area: save;
  outline: 1px solid #C5C5C5;
  width: auto;
}

.product-status-button-disabled {
  background-color: transparent;
  border-radius: 4px;
  outline: transparent;
  border: 0px;
  padding: 5px;
  font-weight: 600;
  cursor: not-allowed;
  color: #C5C5C5;
  padding: 5px;
  border: 1px solid #C5C5C5;
}

.wide {
    grid-template-columns: 75px 1fr 200px;
}

.no-data {
