@media screen and (max-width: 700px) {
  .product-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 7px;
    grid-row-gap: 7px;
    padding-bottom: 10px;
  }

  .product-button-save{
