  }
}

@media screen and (max-width: 550px) {
  .product-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 7px;
    grid-row-gap: 7px;
    padding-bottom: 10px;
  }
  
  .wide {
    grid-template-columns: 75px 1fr 100px;
  }

  .ts-button-container {
    display: flex;
    flex-direction: column;
  }
}
