.range { 
    margin: auto;
    -webkit-appearance: none;
    position: relative;
    overflow: hidden;
    height: 10px;
    width: 150px;
    border-radius: 10px;
}

.pointer {
    cursor: pointer;
}

::-webkit-slider-runnable-track {
    background: #C4C4C4;
}

::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 10px; /* 1 */
    height: 10px;
    background: red;
    box-shadow: -100vw 0 0 100vw red;
}

::-moz-range-track {
    height: 10px;
    background: #ddd;
}

::-moz-range-thumb {
    background: #fff;
    height: 10px;
    width: 10px;
    border: 3px solid #999;
    border-radius: 0 !important;
    box-shadow: -100vw 0 0 100vw red;
    box-sizing: border-box;
}

::-ms-fill-lower { 
    background: red;
}

.lime::-webkit-slider-thumb {
    background: lime;
    box-shadow: -100vw 0 0 100vw lime;
}

.lime::-moz-range-thumb {
    box-shadow: -100vw 0 0 100vw lime;
}

.lime::-ms-fill-lower {
    background-color: lime;
}

.green::-webkit-slider-thumb {
    background: rgb(179, 255, 0);
    box-shadow: -100vw 0 0 100vw rgb(179, 255, 0);
}

.green::-moz-range-thumb {
    box-shadow: -100vw 0 0 100vw rgb(179, 255, 0);
}

.green::-ms-fill-lower {
    background-color: rgb(179, 255, 0);
}

.orange::-webkit-slider-thumb {
    background: orange;
    box-shadow: -100vw 0 0 100vw orange;
}

.orange::-moz-range-thumb {
    box-shadow: -100vw 0 0 100vw orange;
}

.orange::-ms-fill-lower {
    background-color: orange;
}

::-ms-thumb { 
    background: #fff;
    border: 2px solid #999;
    height: 10px;
    width: 10px;
    box-sizing: border-box;
}

::-ms-ticks-after { 
    display: none; 
}

::-ms-ticks-before { 
    display: none; 
}

::-ms-track { 
    background: #fff;
    color: transparent;
    height: 10px;
    border: none;
}

::-ms-tooltip { 
    display: none;
}
