.card-panel {
    background: rgb(0,158,226);
    /* background: linear-gradient(135deg, rgba(0,158,226,1) 0%, rgba(2,215,228,1) 35%, rgba(0,232,190,1) 100%); */
    background: linear-gradient(-45deg, #20cceb, #20cceb, #00ffc8, #23a6d5, #23d5ab);
    animation: gradient 15s ease infinite;
    background-size: 400% 400%;

    position: relative;

    background-color: turquoise;
    color: white;
    width: 330px;
    height: 200px;

    margin: auto;

    border-radius: 20px;

    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.card-num {
    font-size: 20px;
    line-height: 20px;
    height: 25px;
    position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
    letter-spacing: 5px;
    text-align: center;
}

.card-info {
    position: absolute;
    bottom: 10px;
    left: 10px;

    font-size: 15px;
    font-weight: 700;
}