.button {
    margin-top:10px;
    margin-left: 50%;
    transform: translateX(-50%);

    color: white;
    background-color: black;

    display: inline-block;

    height: 40px;
    padding: 10px;
    padding-left: 25px;
    padding-right: 25px;

    border-radius: 15px;
    border-style: none;

    cursor: pointer;

    transition: 0.2s ease;
}

.button:hover {
    background-color: white;
    color:black;
    outline: 2px black solid;
    outline-offset: -2px;
}

.disabled {
    cursor: not-allowed;
    background-color: rgb(199, 199, 199);
    color: gray;
}

.disabled:hover {
    cursor: not-allowed;
    background-color: rgb(199, 199, 199);
    color: gray;
    outline: none;
}

.center {
    text-align: center;
}

.main-container {
    padding-top: 100px;
    padding-bottom: 10px;
    margin: auto;
    min-height: 100vh;
    max-width: 700px;
    overflow: visible;
}

@media screen and (max-width: 700px) {
    .main-container {
        padding-left: 15px;
        padding-right: 15px;
        margin: auto;
        max-width: 700px;
    }
}

.partition-name {
    font-size: 20px;
    font-weight: 700;
    vertical-align: middle;
}

.partition-container {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    height:50px;
    align-items: center
}

.profile-pic {
    width: 50px;
    height: 50px;
    border-radius: 10px;
    margin-right: 10px;
}

.initial-letter {
    text-align: center;
    vertical-align: middle;
    line-height: 75px;
    font-size: 17px;
}

.wide-card {
    width: 100%;
    background: linear-gradient(-45deg, #20cceb, #20cceb, #00ffc8, #2dbdf1, #23d5ab);
    animation: gradient 45s ease infinite;

    background-size: 400% 400%;
    position: relative;
    
    background-color: turquoise;
    color: white;
    height: 200px;
    
    margin: auto;
    
    border-radius: 20px;
    
    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.wc-amount {
    font-size: 20px;
    font-weight: 200;
    position: relative;
    top:30px;
    left:30px;
}

.wc-system {
    position: absolute;
    left:30px;
    bottom: 20px;

    font-weight: 600;
}

.transaction-card {
    margin-bottom: 10px;
    transition: 0.2s ease;

    display: grid;

    grid-template-columns: 75px 1fr 100px;
    grid-template-rows: 40px 1fr;

    grid-template-areas: 
    "img name amount"
    "img desc amount";
}

.transaction-card:hover {
    border-radius: 5px;
    background-color: whitesmoke;
}

.ts-img {
    grid-area: img;
    
    background: rgb(222,222,222);
    
    width:75px;
    height:75px;

    border-radius: 20px;
}

.ts-name {
    grid-area: name;

    margin-top: 10px;
    margin-left: 10px;

    font-size: 18px;
    font-weight: 600;
}

.ts-desc {
    margin-left: 10px;
    position: relative;
    top:-10px;

    font-size: 17px;
    font-weight: 400;

    grid-area: desc;
}

.ts-amount {
    grid-area: amount;

    text-align: end;
    
    display: flex;
    justify-content: right;
    align-items: center;
}

.error-panel {
    width: fit-content;
    color: white;
    background-color: red;
    border-radius: 15px;

    margin: auto;
    margin-top: 10px;
    padding: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.success-panel {
    background-color: limegreen;
}

.red {
    color: red;
}

.green {
    color:#00CF21;
}

.big-font {
    font-weight: 600;
}

.input {
    border:none;
    border-bottom: 1px solid #999;
    width: 100%;
    font-size: 17px;

    transition: 0.2s ease;
}

.input:focus {
    outline: none;
}

.tc-name {
    margin-top: 10px;
    font-size: 17px;
}

.subtext {
    width: 100%;
    text-align: center;
}

.link {
    background-image: linear-gradient(90deg,cyan,rgb(0, 183, 255));
    
    background-size: 0% 2px;
    background-repeat: no-repeat;
    background-position: left bottom;
    transition: background-size 0.3s ease;

    color: black;
    text-decoration: none;
}

.link:hover {
    background-size: 100% 2px;
}

.link:visited {
    color: black;
}

.right {
    float: right;
}

.left {
    float: left;
}