.promo-card {
    width: 100%;
    background: url("https://www.pngkey.com/png/full/7-77287_snow-png-transparent-image-ivory.png") repeat, url("https://www.pngkey.com/png/full/7-77287_snow-png-transparent-image-ivory.png") repeat, linear-gradient(135deg,#20cceb, #23d5ab);
    background-size: 500px 500px, 500px 500px, 100%;

    position: relative;
    height: 200px;
    margin: auto;
    border-radius: 20px;
    
    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);

    animation: snow 10s linear infinite;

    color: white;
}

@keyframes snow {
  0% {background-position: 0px 0px, 0px 0px, 0px 0px;}
  50% {background-position: 500px 500px, 250px 250px, 0px 0px;}
  100% {background-position: 1000px 1000px, 500px 500px, 0px 0px;}
}

.promo-name {
  margin-top: 70px;
  margin-left: 30px;

  font-size:30px;
  font-weight: 700;
}

.promo-description {
  margin-left: 30px;
  font-weight: 300;

  position: relative;
  bottom: 10px;
}

.product-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 7px;
  grid-row-gap: 7px;
  padding-bottom: 10px;
}

.product-card {
  padding: 10px;
  padding-top: 15px;
  padding-bottom: 0px;
  background-color: whitesmoke;
  border-radius: 7px;

  transition: 0.2s ease;
}

.product-card:hover > .product-img{
  transform: translateY(-10px);
}

.product-card:hover > .product-name{
  transform: translateY(-10px);
}

.product-card:hover > .product-price{
  transform: translateY(-10px);
}

.product-card:hover > .product-detail{
  transform: translateY(-10px);
}

.product-card:hover > .product-button{
  opacity: 1.0;
  transform: translateY(-10px);
}

.product-button {
  opacity: 0.0;
  cursor: pointer;
  border-radius: 10px;
  width: 100%;
  transition: 0.2s ease;
  border: 1px solid black;
}

.product-button:hover{
  background-color: rgb(221, 221, 221);
}

.edit-button-container {
  padding-top: 10px;
  padding-bottom: 10px;
}

.edit-button {
  cursor: pointer;
  border-radius: 10px;
  transition: 0.2s ease;
  border: 1px solid black;
  width: 100%;
}

.edit-button:hover {
  background-color: rgb(221, 221, 221);
}

.create-button {
  margin-top: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  border-radius: 10px;
  width: 100%;
  transition: 0.2s ease;
}

.product-card img{
  display: block;
  width: 65%;
  margin-left: auto;
  margin-right: auto;
  transition: 0.2s ease;
}

.product-name {
  font-weight:400;
  font-size: 13pt;
  transition: 0.2s ease;
}

.product-price {
  font-weight:800;
  font-size: 13pt;
  transition: 0.2s ease;
}

.product-detail {
  font-size: 8pt;
  position: relative;
  bottom:5px;
  transition: 0.2s ease;
}

.ts-button-container {
  display: grid;
  grid-template-columns: 1fr 85px 60px;
  grid-template-areas: 
  "edit processing cancel"
  "save save save";

  margin: 1px;
  outline: 1px solid #C5C5C5;
  background-color: #EFEFEF;
  border-radius: 5px;
  cursor: pointer;
}

.psb-edit {
  grid-area: edit;
}

.psb-edit {
  grid-area: processing;
}

.psb-cancel {
  grid-area: cancel;
}

.product-status-button {
  background-color: transparent;
  outline: transparent;
  border: 0px;
  padding: 5px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s ease;
  border-radius: 4px;
}

.product-status-button:hover {
  background-color: #d3d3d3;
}

.product-button-active {
  background-color: black;
  color:white;
}

.product-button-active:hover {
  background-color: black;
  cursor: not-allowed;
}

.product-button-save {
  grid-area: save;
  outline: 1px solid #C5C5C5;
  width: auto;
}

.product-status-button-disabled {
  background-color: transparent;
  border-radius: 4px;
  outline: transparent;
  border: 0px;
  padding: 5px;
  font-weight: 600;
  cursor: not-allowed;
  color: #C5C5C5;
  padding: 5px;
  border: 1px solid #C5C5C5;
}

.wide {
    grid-template-columns: 75px 1fr 200px;
}

.no-data {
  color: gray;
  width: 100%;
  text-align: center;
}

.thin {
  font-weight: 200;
}

.whitesmoked {
  background-color: whitesmoke;
}

.no-bg {
  background-color: transparent;
}

@media screen and (max-width: 700px) {
  .product-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 7px;
    grid-row-gap: 7px;
    padding-bottom: 10px;
  }

  .product-button-save{
    background-color: #e9e9e9;
  }
}

@media screen and (max-width: 550px) {
  .product-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 7px;
    grid-row-gap: 7px;
    padding-bottom: 10px;
  }
  
  .wide {
    grid-template-columns: 75px 1fr 100px;
  }

  .ts-button-container {
    display: flex;
    flex-direction: column;
  }
}