.topbar {
    position: fixed;
    width: 100vw;
    font-weight: 700;
    font-size: 20px;
    border-bottom: 1px solid rgb(212, 212, 212);
    text-align: center;
    padding: 15px;
    padding-left: 20px;
    background-color: rgba(255, 255, 255, 0.5);
    z-index: 1000;
    backdrop-filter: blur(8px);
}

.login {
    cursor: pointer;
    font-weight: 500;
    font-size: 17px;
    position: relative;
    right:40px;
    top: 4px;
}

sup {
    font-weight: 400;
}