.nav-container {
    width: 100%;
    background-color: whitesmoke;
    margin-top:10px;
    padding: 10px;
    padding-left: 0px;
    padding-right: 0px;
    border-radius: 15px;
    outline: 1px solid #838383;
}

.nav-link {
    text-decoration: none;
    color: black;
    padding: 12px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: transparent;
    border-radius: 15px;
    transition: 0.2s ease;
}

.nav-link:hover {
    background-color: black;
    color: white;
    padding-left: 30px;
    padding-right: 30px;
}